<template>
  <div v-nav="{title: ''}">
    <div id="form">
      <group type="checkbox" title="">
        <x-checkbox v-model="v1" required :options="options"  name="checkbox" empty-tips="AB"></x-checkbox>
      </group>
      <div class="tips">{{displayV}}</div>
      <group type="checkbox" title="">
        <x-checkbox v-model="v2" :options="options" name="checkbox2" ></x-checkbox>
      </group>
      <group type="checkbox" title="1-2">
        <x-checkbox v-model="v3" :options="options2" pattern="{1,2}" name="checkbox3" not-match-tips="1-2"></x-checkbox>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit"></x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import XCheckbox from '../components/checkbox'
import XButton from '../components/button'

export default {
  components: {
    Group,
    XCheckbox,
    XButton
  },
  data () {
    return {
      options: [
        {label: 'A', value: 'a'},
        {label: 'B', value: 'b'}
      ],
      options2: [
        {label: '', value: '1'},
        {label: '', value: '2'},
        {label: '', value: '3'},
        {label: ' ()', value: '4', disabled: true}
      ],
      v1: [],
      v2: ['a', 'b'],
      v3: [],
      v4: [],
      displayV: '[]'
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('')
        }
      })
    }
  },
  watch: {
    v1 (val) {
      this.displayV = JSON.stringify(val)
    }
  }
}
</script>

<style scoped>
.tips {
  padding-left:15px;
  line-height:32px;
  color:#808080;
  font-size: 14px;
}
</style>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



