<template>
  <div v-nav="{title: ''}">
    <list :indent="false" title="">
      <list-item @on-click="showAlert1()"></list-item>
      <list-item @on-click="showAlert2()"></list-item>
      <list-item @on-click="showAlert3()"></list-item>
    </list>
    <list :indent="false" title="">
      <list-item @on-click="showAlert4()"></list-item>
      <list-item @on-click="showAlert5()"></list-item>
      <list-item @on-click="showAlert6()"></list-item>
    </list>
    <alert v-model="show1" title="" content=""></alert>
    <alert v-model="show2" content=""></alert>
    <alert v-model="show3" @on-hide="hideEvent()"></alert>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'
import Alert from '../components/alert'

export default {
  components: {
    List,
    ListItem,
    Alert
  },
  data () {
    return {
      show1: false,
      show2: false,
      show3: false
    }
  },
  methods: {
    showAlert1 () {
      this.show1 = true
    },
    showAlert2 () {
      this.show2 = true
    },
    showAlert3 () {
      this.show3 = true
    },
    showAlert4 () {
      this.$alert.show({title: '', content: ''})
    },
    showAlert5 () {
      this.$alert.show('')
    },
    showAlert6 () {
      this.$alert.show({content: '', onHide: () => { this.hideEvent() }})
    },
    hideEvent () {
      this.$toast.show('')
    }
  }
}
</script>



