<template>
  <div v-nav="{title: ''}">
    <div id="form">
      <group type="form" title="Boolean">
        <x-switch v-model="v1" title=""></x-switch>
      </group>
      <div class="tips">{{d1}}</div>
      <group type="form" title="Number">
        <x-switch v-model="v2" title=""></x-switch>
      </group>
      <div class="tips">{{d2}}</div>
      <group type="form" title="">
        <x-switch v-model="v3" title="" disabled></x-switch>
        <x-switch v-model="v4" title=""></x-switch>
      </group>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import XSwitch from '../components/switch'

export default {
  components: {
    Group,
    XSwitch
  },
  data () {
    return {
      v1: true,
      v2: 0,
      v3: false,
      v4: 0,
      d1: 'true',
      d2: '0'
    }
  },
  watch: {
    v1 (val) {
      this.d1 = JSON.stringify(val)
    },
    v2 (val) {
      this.d2 = JSON.stringify(val)
    }
  }
}
</script>

<style scoped>
.tips {
  padding-left:15px;
  line-height:32px;
  color:#808080;
  font-size: 14px;
}
</style>



