<template>
  <div v-nav="{title: ''}">
    <div id="form">
      <group type="form" title="">
        <time-picker v-model="v1" title="" required placeholder=""></time-picker>
      </group>
      <group type="form" title="">
        <time-picker v-model="v2" title="" auto-complete></time-picker>
      </group>
      <group type="form" title="">
        <time-picker v-model="v3" title=""></time-picker>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit"></x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import TimePicker from '../components/time-picker'
import XButton from '../components/button'

export default {
  components: {
    Group,
    TimePicker,
    XButton
  },
  data () {
    return {
      v1: '',
      v2: '',
      v3: '12:12'
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



