<template>
  <div v-nav="{title: 'Toast'}">
    <list :indent="false" title="()">
      <list-item @on-click="toast1()">text</list-item>
      <list-item @on-click="toast2('top')"></list-item>
      <list-item @on-click="toast2('bottom')"></list-item>
      <list-item @on-click="toast2('middle')"></list-item>
      <list-item @on-click="toast2('default')"></list-item>
      <list-item @on-click="toast3('success', '')"></list-item>
      <list-item @on-click="toast3('cancel', '')"></list-item>
      <list-item @on-click="toast3('warn', '')"></list-item>
      <list-item @on-click="toast3('text', '')"></list-item>
      <list-item @on-click="toast4()"></list-item>
      <list-item @on-click="toast5()">3s(2s)</list-item>
    </list>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'

export default {
  components: {
    List,
    ListItem
  },
  methods: {
    toast1 () {
      this.$toast.show('')
    },
    toast2 (position) {
      this.$toast.show({text: '', position: position})
    },
    toast3 (type, text) {
      this.$toast.show({text: text, position: 'middle', type: type})
    },
    toast4 () {
      this.$toast.show({text: '', width: '220px'})
    },
    toast5 () {
      this.$toast.show({text: '', time: 3000})
    }
  }
}
</script>



