<template>
  <div v-nav="{title: 'Tooltip'}">
    <list :indent="false" title="()">
      <list-item @on-click="show1()"></list-item>
      <list-item @on-click="show2()"></list-item>
      <list-item @on-click="show3()">class</list-item>
    </list>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'

export default {
  components: {
    List,
    ListItem
  },
  methods: {
    show1 () {
      this.$tooltip.show('')
    },
    show2 () {
      this.$tooltip.show({content: '', duration: 3000})
    },
    show3 () {
      this.$tooltip.show({content: '', className: 'top-44'})
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px;
}
.top-44 {
  top: 44px;
}
</style>



