<template>
  <div v-nav="{title: ''}">
    <swiper>
      <swiper-item class="page"><h2 class="title fadeInUp animated"></h2></swiper-item>
      <swiper-item class="page"><h2 class="title fadeInUp animated"></h2></swiper-item>
      <swiper-item class="page"><h2 class="title fadeInUp animated"></h2></swiper-item>
      <swiper-item class="page"><h2 class="title fadeInUp animated"></h2></swiper-item>
      <swiper-item class="page"><h2 class="title fadeInUp animated"></h2></swiper-item>
      <swiper-item class="page"><h2 class="title fadeInUp animated"></h2></swiper-item>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperItem } from '../components/swiper'

export default {
  components: {
    Swiper,
    SwiperItem
  }
}
</script>

<style scoped>
.page{
    color: #ffffff;
    background-color: #000000;
}
.title{
    margin-top: 250px;
    text-align: center;
}
.x-swiper-item.active .animated {
  animation-fill-mode: both;
  opacity: 1;
}
.x-swiper-item:not(.active) .animated {
  animation: none;
  opacity: 0;
}
.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}
@-webkit-keyframes fadeInUp {
  from {
      opacity: 0;
      transform: translate3d(0, 100%, 0);
  }
  100% {
      opacity: 1;
      transform: none;
  }
}
@keyframes fadeInUp {
  from {
      opacity: 0;
      transform: translate3d(0, 100%, 0);
  }
  100% {
      opacity: 1;
      transform: none;
  }
}
.fadeInUp {
  animation-name: fadeInUp;
}
</style>



