import $ from '../core/util';
import tplItem from './item.html';
import compress from './image';
import upload from './upload';

let _id = 0;

/**
 * uploader 
 * @param {string} selector selector
 * @param {object} options 
 * @param {string} [options.url] urljson
 * @param {boolean} [options.auto=true] `true`this.upload()example
 * @param {string} [options.type=file] , `file`; `base64`base64
 * @param {string=} [options.fileVal=file] name
 * @param {object=} [options.compress] , `false`
 * @param {number=} [options.compress.width=1600] 
 * @param {number=} [options.compress.height=1600] 
 * @param {number=} [options.compress.quality=.8] ,  0 ~ 1
 * @param {function=} [options.onBeforeQueued] return false
 * @param {function=} [options.onQueued] 
 * @param {function=} [options.onBeforeSend] example
 * @param {function=} [options.onSuccess] 
 * @param {function=} [options.onProgress] 
 * @param {function=} [options.onError] 
 *
 * @example
 * var uploadCount = 0;
 * weui.uploader('#uploader', {
 *    url: 'http://localhost:8081',
 *    auto: true,
 *    type: 'file',
 *    fileVal: 'fileVal',
 *    compress: {
 *        width: 1600,
 *        height: 1600,
 *        quality: .8
 *    },
 *    onBeforeQueued: function(files) {
 *        // `this` , `files` 
 *
 *        if(["image/jpg", "image/jpeg", "image/png", "image/gif"].indexOf(this.type) < 0){
 *            weui.alert('');
 *            return false; // 
 *        }
 *        if(this.size > 10 * 1024 * 1024){
 *            weui.alert('10M');
 *            return false;
 *        }
 *        if (files.length > 5) { // 
 *            weui.alert('5');
 *            return false;
 *        }
 *        if (uploadCount + 1 > 5) {
 *            weui.alert('5');
 *            return false;
 *        }
 *
 *        ++uploadCount;
 *
 *        // return true; // 
 *    },
 *    onQueued: function(){
 *        console.log(this);
 *
 *        // console.log(this.status); // 'ready', 'progress', 'success', 'fail'
 *        // console.log(this.base64); // base64file.base64base64
 *
 *        // this.upload(); // upload
 *        // this.stop(); // 
 *
 *        // return true; // 
 *    },
 *    onBeforeSend: function(data, headers){
 *        console.log(this, data, headers);
 *        // $.extend(data, { test: 1 }); // 
 *        // $.extend(headers, { Origin: 'http://127.0.0.1' }); // 
 *
 *        // return false; // 
 *    },
 *    onProgress: function(procent){
 *        console.log(this, procent);
 *        // return true; // 
 *    },
 *    onSuccess: function (ret) {
 *        console.log(this, ret);
 *        // return true; // 
 *    },
 *    onError: function(err){
 *        console.log(this, err);
 *        // return true; // 
 *    }
 * });
 */
function uploader(selector, options) {
    const $uploader = $(selector);
    const URL = window.URL || window.webkitURL || window.mozURL;

    // DOMfile-content
    function findFileCtn($uploader, id){
        const $file = $uploader.find('[data-id="' + id + '"]');
        let $fileCtn = $file.find('.weui-uploader__file-content');

        if(!$fileCtn.length){
            $fileCtn = $('<div class="weui-uploader__file-content"></div>');
            $file.append($fileCtn);
        }
        $file.addClass('weui-uploader__file_status');
        return $fileCtn;
    }

    // DOM
    function clearFileStatus($uploader, id){
        const $file = $uploader.find('[data-id="' + id + '"]').removeClass('weui-uploader__file_status');
        $file.find('.weui-uploader__file-content').remove();
    }

    // 
    function setUploadFile(file) {
        file.url = URL.createObjectURL(file);
        file.status = 'ready';
        file.upload = function () {
            upload($.extend({
                $uploader: $uploader,
                file: file
            }, options));
        };
        file.stop = function(){
            this.xhr.abort();
        };

        options.onQueued(file);
        if(options.auto) file.upload();
    }

    options = $.extend({
        url: '',
        auto: true,
        type: 'file',
        fileVal: 'file',
        xhrFields: {},
        onBeforeQueued: $.noop,
        onQueued: $.noop,
        onBeforeSend: $.noop,
        onSuccess: $.noop,
        onProgress: $.noop,
        onError: $.noop
    }, options);

    if(options.compress !== false){
        options.compress = $.extend({
            width: 1600,
            height: 1600,
            quality: .8
        }, options.compress);
    }

    if(options.onBeforeQueued){
        const onBeforeQueued = options.onBeforeQueued;
        options.onBeforeQueued = function(file, files){
            const ret = onBeforeQueued.call(file, files);
            if(ret === false){
                return false;
            }
            if(ret === true){
                return;
            }

            const $item = $($.render(tplItem, {
                id: file.id
            }));

            $uploader.find('.weui-uploader__files').append($item);
        };
    }
    if(options.onQueued){
        const onQueued = options.onQueued;
        options.onQueued = function(file){
            if(!onQueued.call(file)){
                const $file = $uploader.find('[data-id="' + file.id + '"]');
                $file.css({
                    backgroundImage: 'url("' + (file.base64 || file.url) + '")'
                });
                if(!options.auto) {
                    clearFileStatus($uploader, file.id);
                }
            }
        };
    }
    if(options.onBeforeSend){
        const onBeforeSend = options.onBeforeSend;
        options.onBeforeSend = function(file, data, headers){
            const ret = onBeforeSend.call(file, data, headers);
            if(ret === false){
                return false;
            }
        };
    }
    if(options.onSuccess){
        const onSuccess = options.onSuccess;
        options.onSuccess = function(file, ret){
            file.status = 'success';
            if(!onSuccess.call(file, ret)){
                clearFileStatus($uploader, file.id);
            }
        };
    }
    if(options.onProgress){
        const onProgress = options.onProgress;
        options.onProgress = function(file, percent){
            if(!onProgress.call(file, percent)){
                findFileCtn($uploader, file.id).html(percent + '%');
            }
        };
    }
    if(options.onError){
        const onError = options.onError;
        options.onError = function(file, err){
            file.status = 'fail';
            if(!onError.call(file, err)){
                findFileCtn($uploader, file.id).html('<i class="weui-icon-warn"></i>');
            }
        };
    }
    
    $uploader.find('input[type="file"]').on('change', function (evt) {
        const files = evt.target.files;

        if (files.length === 0) {
            return;
        }

        if(options.compress === false && options.type == 'file'){
            // 
            Array.prototype.forEach.call(files, (file) => {
                file.id = ++_id;

                if(options.onBeforeQueued(file, files) === false) return;

                setUploadFile(file);
            });
        }else{
            // base64  
            Array.prototype.forEach.call(files, (file) => {
                file.id = ++_id;

                if(options.onBeforeQueued(file, files) === false) return;
                
                compress(file, options, function(blob){
                    if(blob) setUploadFile(blob);
                });
            });
        }

        this.value = '';
    });
}
export default uploader;



