<template>
  <div class="weui-cell" :class="{'weui-cell_access': !!link || allowAccess, 'no-indent': !$parent.indent}" @click.prevent="onClicked">
    <div class="weui-cell__hd" :style="headerStyle">
      <slot name="icon"></slot>
    </div>
    <div class="weui-cell__bd" :style="bodyStyle">
      <slot></slot>
    </div>
    <div class="weui-cell__ft" :style="footerStyle"  :class="{'no-forward-arrow': $parent.hideForwardArrow}">
      {{desc}}
      <slot name="desc"></slot>
    </div>
  </div>
</template>

<script>
import { go } from '../core/router'

export default {
  name: 'list-item',
  props: {
    title: String,
    desc: [String, Number],
    headerStyle: [String, Object],
    bodyStyle: [String, Object],
    footerStyle: [String, Object],
    link: {
      type: [String, Object]
    },
    allowAccess: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    onClicked () {
      if (this.link) {
        go(this.link, this.$router)
      } else {
        this.$emit('on-click')
      }
    }
  }
}
</script>

<style lang="less">
@import '../../styles/weui/widget/weui-cell/weui-access';
.weui-cell{
  &.no-indent {
    &:before {
      left:0;
    }
  }
}
.weui-cell_access .weui-cell__ft{
  &.no-forward-arrow {
    padding-right:0;
    position:static;
    &:after {
      display:none;
    }
  }
}
</style>



