import $ from '../core/util';
import cron from '../picker/cron';
import $picker from '../picker'

function datetimePicker(options) {
    const defaults = $.extend({
        id: 'datetimePicker',
        onChange: $.noop,
        onConfirm: $.noop,
        start: 2000,
        end: 2030,
        cron: '* * *'
    }, options);

    if (typeof defaults.start === 'number') {
        defaults.start = new Date(`${defaults.start}-01-01`);
    }
    else if (typeof defaults.start === 'string') {
        defaults.start = new Date(defaults.start);
    }
    if (typeof defaults.end === 'number') {
        defaults.end = new Date(`${defaults.end}-12-31`);
    }
    else if (typeof defaults.end === 'string') {
        defaults.end = new Date(defaults.end);
    }

    const findBy = (array, key, value) => {
        for(let i = 0, len = array.length; i < len; i++){
            const obj = array[i];
            if(obj[key] == value){
                return obj;
            }
        }
    };

    const getHours = () => {
        let hours = [];
        for (let i = 0; i < 24; i++) {
          if (i < 10) {
            hours.push({
              label: `0${i}`,
              value: i,
              children: getMins()
            });
          } else {
            hours.push({
              label: `${i}`,
              value: i,
              children: getMins()
            });
          }
        }
        return hours;
    };

    const getMins = () => {
        let mins = [];
        for (let i = 0; i < 60; i++) {
          if (i < 10) {
            mins.push({
              label: `0${i}`,
              value: i
            });
          } else {
            mins.push({
              label: `${i}`,
              value: i
            });
          }
        }
        return mins;
    };

    const date = [];
    const interval = cron.parse(defaults.cron, defaults.start, defaults.end);
    const time = getHours();
    let obj;
    do {
        obj = interval.next();

        const year = obj.value.getFullYear();
        const month = obj.value.getMonth() + 1;
        const day = obj.value.getDate();

        let Y = findBy(date, 'value', year);
        if (!Y) {
            Y = {
                label: year + '',
                value: year,
                children: []
            };
            date.push(Y);
        }
        let M = findBy(Y.children, 'value', month);
        if (!M) {
            M = {
                label: month + '',
                value: month,
                children: []
            };
            Y.children.push(M);
        }
        M.children.push({
            label: day + '',
            value: day,
            children: time
        });
    }
    while (!obj.done);

    return $picker.picker(date, defaults);
}

export default datetimePicker;



