<template>
  <div v-nav="{title: ''}">
    <div id="form">
      <group type="form" title="">
        <x-textarea title="" placeholder="" required :max-length="200"></x-textarea>
      </group>
      <group type="form" title="">
        <x-textarea title="" placeholder="" required :show-counter="false"></x-textarea>
      </group>
      <group type="form" title="porp:autosize">
        <x-textarea title="" placeholder="" required autosize :max-length="200"></x-textarea>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit"></x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import XTextarea from '../components/textarea'
import XButton from '../components/button'

export default {
  components: {
    Group,
    XTextarea,
    XButton
  },
  data () {
    return {
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



