<template>
  <div v-nav="{title: 'Loading'}">
    <list :indent="false" title="()">
      <list-item @on-click="loading()"></list-item>
    </list>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'

export default {
  components: {
    List,
    ListItem
  },
  methods: {
    loading () {
      this.$loading.show('')
      setTimeout(() => {
        this.$loading.hide()
      }, 2000)
    }
  }
}
</script>



