<template>
  <div v-nav="{title: ''}">
    <list title="">
      <list-item :link="{path: '/demo/badge'}"><p> <badge text="3"></badge></p></list-item>
      <list-item :link="{path: '/demo/badge'}" desc=""><p> <badge text="3"></badge></p></list-item>
      <list-item :link="{path: '/demo/badge'}" desc=""><p> <badge text="new"></badge></p></list-item>
      <list-item desc="" :link="{path: '/demo/badge'}"><p></p><badge slot="desc"></badge></list-item>
      <list-item :link="{path: '/demo/badge'}" header-style="position: relative;margin-right:10px;">
      <div slot="icon" style="height:50px;width:50px;display:block;background-color:#ddd;">
        <badge text="8" style="position: absolute;top: -.4em;right: -.4em;"></badge>
      </div>
      <p></p>
      <p style="font-size:12px;color:#aaa"></p>
      </list-item>
    </list>
    <list title="">
      <list-item> TabBar Demo</list-item>
    </list>
     &nbsp;&nbsp;&nbsp;<badge text="3"></badge>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'
import Badge from '../components/badge'

export default {
  components: {
    List,
    ListItem,
    Badge
  }
}
</script>



