<template>
  <div v-nav="{title: ''}">
    <div id="form">
      <group type="form" title="">
         <image-uploader :options="{url: uploadUrl}" @on-success="uploaderSuccess" @on-error="uploaderError" @on-delete="uploaderDelete"></image-uploader>
      </group>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import ImageUploader from '../components/image-uploader'

export default {
  components: {
    Group,
    ImageUploader
  },
  data () {
    return {
      uploadUrl: '/file/uploadForServer2' // http://fastdfs.sit.vipshop.com:80/file/uploadForServer2
    }
  },
  methods: {
    uploaderSuccess (res) {
      this.$toast.show('')
      console.log(res)
    },
    uploaderError (err) {
      this.$toast.show(err)
    },
    uploaderDelete (fileId, target, gallery) {
      target.remove()
      gallery.hide()
    }
  }
}
</script>



