<template>
  <div v-nav="{title: ''}">
    <panel header="" :indent="false">
      <panel-item title="" desc="" :link="{path: '/demo/panel'}"></panel-item>
      <panel-item title="" desc="" :link="{path: '/demo/panel'}"></panel-item>
    </panel>
    <panel header="">
      <panel-item :img-src="img" title="" desc="" :link="{path: '/demo/panel'}"></panel-item>
      <panel-item :img-src="img" title="" desc="" :link="{path: '/demo/panel'}"></panel-item>
    </panel>
    <panel :header="header" footer="" :more-link="{path: '/demo/panel'}">
      <panel-item :img-src="img" title="" desc=""></panel-item>
    </panel>
    <panel :header="header" footer="" :more-link="{path: '/demo/panel'}" :card="true">
      <panel-item :img-src="img" title="" desc=""></panel-item>
    </panel>
  </div>
</template>

<script>
import { Panel, PanelItem } from '../components/panel'

export default {
  components: {
    Panel,
    PanelItem
  },
  data () {
    return {
      header: '<span style="padding:0 2px;border:1px solid #F53D3D;color:#F53D3D;border-radius:2px;"></span>',
      img: 'data:image/png;base64,'
    }
  }
}
</script>



