<template>
  <div v-nav="{title: ''}">
    <div id="form">
      <group type="form" title="">
        <date-picker v-model="v1" title=""></date-picker>
      </group>
      <group type="form" title="">
        <date-picker v-model="v2" title="" :start="2016" :end="2018" required placeholder=""></date-picker>
      </group>
      <group type="form" title="">
        <date-picker v-model="v3" title="" auto-complete></date-picker>
      </group>
      <group type="form" title="">
        <date-picker v-model="v4" title=""></date-picker>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit"></x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import DatePicker from '../components/date-picker'
import XButton from '../components/button'

export default {
  components: {
    Group,
    DatePicker,
    XButton
  },
  data () {
    return {
      v1: '',
      v2: '',
      v3: '',
      v4: '2018/1/1'
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



