<template>
  <div v-nav="{title: ''}">
    <grid style="margin-top:10px;">
      <grid-item label="chevron-left"><icon name="chevron-left" slot="icon"></icon></grid-item>
      <grid-item label="commonet-o"><icon name="commonet-o" slot="icon"></icon></grid-item>
      <grid-item label="ellipsis-h"><icon name="ellipsis-h" slot="icon"></icon></grid-item>
