      <grid-item label="eye"><icon name="eye" slot="icon"></icon></grid-item>
      <grid-item label="group"><icon name="group" slot="icon"></icon></grid-item>
      <grid-item label="home"><icon name="home" slot="icon"></icon></grid-item>
