<script>
import { Flexbox, FlexboxItem } from '../components/flexbox'
import { Grid, GridItem } from '../components/grid'
import Icon from '../components/icon'

export default {
  components: {
    Flexbox,
    FlexboxItem,
    Grid,
    GridItem,
    Icon
  }
}
</script>

<style>
svg{
  color: #000;
}
.weui-grid__icon {
  text-align: center;
}
</style>



