<template>
  <div v-nav="{title: ''}">
    <search-box @on-submit="search" v-model="keyword" style="position: absolute; top: 45px;"></search-box>
    <scroller ref="scroller" style="top:88px;" :contentStyle="{'padding-bottom':'89px'}">
      <list hide-forward-arrow :indent="false">
        <list-item v-for="(item, index) in data" :key="index" header-style="margin-right:10px;color:#bbb;">
          <icon name="map-marker" scale="1.0" slot="icon"></icon>
          <p>{{item.name}}</p>
          <p style="color:#aaa;">{{item.addr}}</p>
        </list-item>
      </list>
    </scroller>
  </div>
</template>

<script>
import SearchBox from '../components/search-box'
import Scroller from '../components/scroll'
import { List, ListItem } from '../components/list'
import Icon from '../components/icon'

export default {
  name: 'sear',
  components: {
    SearchBox,
    Scroller,
    List,
    ListItem,
    Icon
  },
  data () {
    return {
      keyword: '',
      temp: [
        {name: '()', addr: '6'},
        {name: '', addr: ''},
        {name: '()', addr: '1;3;10;66;541;544;8'},
        {name: '()', addr: '295'}
      ],
      data: []
    }
  },
  methods: {
    search () {
      if (this.$refs.scroller) this.$refs.scroller.scrollTo(0, 0)
      setTimeout(() => {
        this.data = this.temp
        if (this.$refs.scroller) this.$refs.scroller.finishPullToRefresh()
      }, 500)
    }
  }
}
</script>



