<template>
  <div class="x-tab-item" :class="[currentSelected ? activeClass : '', {'x-tab-selected': currentSelected, 'x-tab-disabled': disabled}]" :style="style" @click="onItemClick(true)">
    <slot></slot>
  </div>
</template>

<script>
import { childMixin } from '../core/mixin'

export default {
  name: 'tabitem',
  mixins: [childMixin],
  props: {
    activeClass: String,
    disabled: Boolean
  },
  computed: {
    style () {
      return {
        borderWidth: this.$parent.lineWidth + 'px',
        borderColor: this.$parent.activeColor,
        color: this.currentSelected ? this.$parent.activeColor : this.disabled ? this.$parent.disabledColor : this.$parent.defaultColor,
        border: this.$parent.animate ? 'none' : 'auto'
      }
    }
  }
}
</script>



