<template>
  <div v-nav="{title: ''}">
    <accordion title="" style="padding-top:10px;">
      <accordion-item type="list"><p></p></accordion-item>
      <accordion-item type="list"><p></p></accordion-item>
    </accordion>
    <accordion title="" :expanded="true">
      <accordion-item desc="" type="list"><p></p></accordion-item>
      <accordion-item desc="" type="list"><p></p></accordion-item>
    </accordion>
    <accordion title="" :expanded="true" style="padding-top:10px;">
      <accordion-item desc="" type="list"><p></p></accordion-item>
      <accordion-item desc="" type="list"><p></p></accordion-item>
    </accordion>
    <accordion title="" :expanded="true">
      <div style="padding:10px 15px;color:#ff0000;"></div>
    </accordion>
    <accordion title="" :expanded="true" style="padding-top:10px;">
      <img slot="icon" width="20" style="display:block;margin-right:5px;" :src="img">
      <div style="padding:10px 15px;"></div>
    </accordion>
    <accordion :expanded="true">
      <span slot="header" style="color:#ff0000;"></span>
      <div style="padding:10px 15px;"></div>
    </accordion>
  </div>
</template>

<script>
import { Accordion, AccordionItem } from '../components/accordion'

export default {
  components: {
    Accordion,
    AccordionItem
  },
  data () {
    return {
      img: 'data:image/png;base64,'
    }
  },
  methods: {
  }
}
</script>



