<template>
  <div v-nav="{title: ''}">
    <x-line>(propslist)</x-line>
    <carousel show-mask :list="data"></carousel>
    <x-line>(component:carousel-item)</x-line>
    <carousel show-mask>
      <carousel-item :img="carouselImg1" title=""></carousel-item>
      <carousel-item :img="carouselImg2" title=""></carousel-item>
      <carousel-item :img="carouselImg3" title=""></carousel-item>
    </carousel>
    <x-line>(props:aspect-ratio)</x-line>
    <carousel show-mask :list="data" :aspect-ratio="0.4" style="margin: 0 10px;"></carousel>
    <div style="margin-bottom:40px;"></div>
    <x-line>(props:loop)</x-line>
    <div style="margin-bottom:10px;text-align:center;color:#999999;"> on-change: {{index}}</div>
    <carousel loop :list="data" dots-position="center" @on-change="changeEvn"></carousel>
    <x-line>(v-model)</x-line>
    <carousel v-model="current" loop :list="data" dots-position="left"></carousel>
    <x-line>(props:link)</x-line>
    <div style="margin-bottom:10px;text-align:center;color:#999999;">$e:on-clickcarousel-item</div>
    <carousel show-mask>
      <carousel-item :img="carouselImg1" title="" @on-click="onClick1"></carousel-item>
      <carousel-item :img="carouselImg2" title="" @on-click="onClick2"></carousel-item>
      <carousel-item :img="carouselImg3" title="" :link="{path:'/demo'}"></carousel-item>
    </carousel>
    <x-line></x-line>
    <carousel loop height="100px">
      <carousel-item><h2 class="title"></h2></carousel-item>
      <carousel-item><h2 class="title"></h2></carousel-item>
      <carousel-item><h2 class="title"></h2></carousel-item>
      <carousel-item><h2 class="title"></h2></carousel-item>
      <carousel-item><h2 class="title"></h2></carousel-item>
      <carousel-item><h2 class="title"></h2></carousel-item>
    </carousel>
    <div style="margin-bottom:40px;"></div>
  </div>
</template>

<script>
import XLine from '../components/line'
import { Carousel, CarouselItem } from '../components/carousel'
import img1 from '../assets/carousel/1.jpg'
import img2 from '../assets/carousel/2.jpg'
import img3 from '../assets/carousel/3.jpg'

export default {
  components: {
    XLine,
    Carousel,
    CarouselItem
  },
  data () {
    return {
      carouselImg1: img1,
      carouselImg2: img2,
      carouselImg3: img3,
      data: [{img: img1, title: ''}, {img: img2, title: ''}, {img: img3, title: ''}],
      index: 0,
      current: 1
    }
  },
  methods: {
    changeEvn (index) {
      this.index = index
    },
    onClick1 () {
      this.$toast.show('')
    },
    onClick2 () {
      this.$toast.show('')
    }
  }
}
</script>

<style scoped>
.title{
  line-height: 100px;
  text-align: center;
  color: #fff;
  background-color: #000;
}
</style>



