<template>
  <div class="x-carousel-item">
    <slot></slot>
    <a href="javascript:" v-if="img" @click.prevent="onClick">
      <div class="x-carousel-img" :style="style"></div>
      <p class="x-carousel-desc" v-if="$parent.showMask" v-html="title"></p>
    </a>
  </div>
</template>

<script>
import { go } from '../core/router'

export default {
  name: 'carousel-item',
  props: {
    img: String,
    title: String,
    link: Object
  },
  computed: {
    style () {
      return {
        backgroundImage: `url(${this.img})`
      }
    }
  },
  methods: {
    onClick () {
      if (this.link) {
        go(this.link, this.$router)
      } else {
        this.$emit('on-click')
      }
    }
  }
}
</script>



