<template>
  <div v-nav="{title: ''}">
    <div id="form">
      <group type="form" title="">
        <x-select v-model="dv1" :data="data1" title="" placeholder="" required></x-select>
      </group>
      <span style="padding-left:15px;line-height:30px;">{{dv1}}</span>
      <group type="form" title="">
        <x-select v-model="dv2" :data="data1" placeholder=""></x-select>
      </group>
      <group type="form" title="">
        <x-select v-model="dv3" :data="data1"  title="" placeholder="" :defaultDispalyText="dl3"></x-select>
      </group>
      <group type="form" title="(porp:depth=3)">
        <x-select v-model="dv4" :data="data2"  title="" placeholder="" :defaultValue="[0,0,1]" :depth="3" @on-select="onselect" :defaultDispalyText="dv4"></x-select>
      </group>
      <p style="padding-left:15px;line-height:30px;color:#aaa;">on-select</p>
      <group type="form" title="event:on-select">
        <x-select title="" placeholder="" @on-select="onselect2"></x-select>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit"></x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import XSelect from '../components/select'
import XButton from '../components/button'

export default {
  components: {
    Group,
    XSelect,
    XButton
  },
  data () {
    return {
      data1: [
        {label: '', value: 0},
        {label: '', value: 1},
        {label: '', value: 2},
        {label: '', value: 3},
        {label: '', value: 4}
      ],
      data2: [{
        label: '',
        value: 0,
        children: [{
          label: '',
          value: 0,
          children: [{
            label: '',
            value: 0
          }, {
            label: '',
            value: 1
          }]
        }, {
          label: '',
          value: 1,
          children: [{
            label: '',
            value: 0
          }, {
            label: '',
            value: 1
          }]
        }]
      },
      {
        label: '',
        value: 1,
        children: [{
          label: '',
          value: 0,
          children: [{
            label: '',
            value: 0
          }, {
            label: '',
            value: 1
          }]
        }, {
          label: '',
          value: 1,
          children: [{
            label: '',
            value: 0
          }, {
            label: '',
            value: 1
          }]
        }]
      }],
      dv1: '',
      dv2: '',
      dv3: 3,
      dl3: '',
      dv4: ''
    }
  },
  methods: {
    onselect (obj) {
      this.dv4 = obj[0].label + obj[1].label + obj[2].label
    },
    onselect2 () {
      this.$toast.show('')
    },
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



