import Vue from 'vue'
import Router from 'vue-router'

import Home from '../demo/Home'
import Demo from '../demo/Demo'
import List from '../demo/List'
import Grid from '../demo/Grid'
import Flex from '../demo/Flex'
import Panel from '../demo/Panel'
import Media from '../demo/Media'
import Scroll from '../demo/Scroll'
import Footer from '../demo/Footer'
import Accordion from '../demo/Accordion'
import Table from '../demo/Table'

import Button from '../demo/Button'
import Badge from '../demo/Badge'
import Icon from '../demo/Icon'
import Dialog from '../demo/Dialog'
import Alert from '../demo/Alert'
import Confirm from '../demo/Confirm'
import Toast from '../demo/Toast'
import Loading from '../demo/Loading'
import ActionSheet from '../demo/ActionSheet'
import Tooltip from '../demo/Tooltip'
import Popup from '../demo/Popup'
import Line from '../demo/Line'
import Rater from '../demo/Rater'
import Progress from '../demo/Progress'
import Slider from '../demo/Slider'
import Carousel from '../demo/Carousel'
import Swiper from '../demo/Swiper'
import Marquee from '../demo/Marquee'
import SearchBox from '../demo/SearchBox'

import Input from '../demo/Input'
import Textarea from '../demo/Textarea'
import Select from '../demo/Select'
import Radio from '../demo/Radio'
import Checkbox from '../demo/Checkbox'
import Switch from '../demo/Switch'
import DatePicker from '../demo/DatePicker'
import TimePicker from '../demo/TimePicker'
import DatetimePicker from '../demo/DatetimePicker'
import LabelRadio from '../demo/LabelRadio'
import LabelCheckbox from '../demo/LabelCheckbox'
import ImageUploader from '../demo/ImageUploader'

Vue.use(Router)

const routes = [
  { path: '/', component: Home },
  { path: '/home', component: Home },
  { path: '/demo', component: Demo },
  { path: '/demo/list', component: List },
  { path: '/demo/grid', component: Grid },
  { path: '/demo/flex', component: Flex },
  { path: '/demo/panel', component: Panel },
  { path: '/demo/media', component: Media },
  { path: '/demo/scroll', component: Scroll },
  { path: '/demo/footer', component: Footer },
  { path: '/demo/accordion', component: Accordion },
  { path: '/demo/table', component: Table },
  { path: '/demo/button', component: Button },
  { path: '/demo/icon', component: Icon },
  { path: '/demo/badge', component: Badge },
  { path: '/demo/dialog', component: Dialog },
  { path: '/demo/alert', component: Alert },
  { path: '/demo/confirm', component: Confirm },
  { path: '/demo/toast', component: Toast },
  { path: '/demo/tooltip', component: Tooltip },
  { path: '/demo/loading', component: Loading },
  { path: '/demo/actionsheet', component: ActionSheet },
  { path: '/demo/popup', component: Popup },
  { path: '/demo/line', component: Line },
  { path: '/demo/rater', component: Rater },
  { path: '/demo/progress', component: Progress },
  { path: '/demo/slider', component: Slider },
  { path: '/demo/carousel', component: Carousel },
  { path: '/demo/swiper', component: Swiper },
  { path: '/demo/marquee', component: Marquee },
  { path: '/demo/input', component: Input },
  { path: '/demo/textarea', component: Textarea },
  { path: '/demo/select', component: Select },
  { path: '/demo/radio', component: Radio },
  { path: '/demo/checkbox', component: Checkbox },
  { path: '/demo/switch', component: Switch },
  { path: '/demo/datepicker', component: DatePicker },
  { path: '/demo/timepicker', component: TimePicker },
  { path: '/demo/datetimepicker', component: DatetimePicker },
  { path: '/demo/labelradio', component: LabelRadio },
  { path: '/demo/labelcheckbox', component: LabelCheckbox },
  { path: '/demo/imageuploader', component: ImageUploader },
  { path: '/demo/searchbox', component: SearchBox }
]

export default new Router({
  routes
})



