<template>
  <div v-nav="{title: ''}">
    <list :indent="false" title="">
      <list-item @on-click="showAlert1()"></list-item>
      <list-item @on-click="showAlert2()"></list-item>
      <list-item @on-click="showAlert3()">/</list-item>
    </list>
    <list :indent="false" title="">
      <list-item @on-click="showAlert4()"></list-item>
      <list-item @on-click="showAlert5()"></list-item>
      <list-item @on-click="showAlert6()">/</list-item>
    </list>
    <confirm v-model="show1" title="" content=""></confirm>
    <confirm v-model="show2" content=""></confirm>
    <confirm v-model="show3" @on-confirm="confirmEvent()" @on-cancel="cancelEvent()"></confirm>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'
import Confirm from '../components/confirm'

export default {
  components: {
    List,
    ListItem,
    Confirm
  },
  data () {
    return {
      show1: false,
      show2: false,
      show3: false
    }
  },
  methods: {
    showAlert1 () {
      this.show1 = true
    },
    showAlert2 () {
      this.show2 = true
    },
    showAlert3 () {
      this.show3 = true
    },
    showAlert4 () {
      this.$confirm.show({title: '', content: ''})
    },
    showAlert5 () {
      this.$confirm.show('')
    },
    showAlert6 () {
      this.$confirm.show({content: '', onConfirm: () => { this.confirmEvent() }, onCancel: () => { this.cancelEvent() }})
    },
    confirmEvent () {
      this.$toast.show('')
    },
    cancelEvent () {
      this.$toast.show('')
    }
  }
}
</script>



