<template>
  <div>
    <div class="weui-cells__title" v-if="title" :style="titleStyle" v-html="title"></div>
    <div class="weui-cells">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'list',
  props: {
    title: String,
    titleStyle: [String, Object],
    indent: {
      type: Boolean,
      default: true
    },
    hideForwardArrow: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="less">
  @import '../../styles/weui/widget/weui-cell/weui-cell_global';
  @import '../../styles/xui/x-list';
</style>



