      <label class="weui-label" v-text="title" :style="labelStyle || $parent.labelStyle"></label>
      <slot name="label"></slot>
    </div>
    <div class="weui-cell__bd">
      <input type="text" class="weui-input" v-model="currentValue"
        :placeholder="placeholder"
        :required="required"
        :emptytips="emptyTips"
        readonly="readonly"
        :style="inputStyle || $parent.inputStyle" ref="timepicker"/>
    </div>
    <div class="weui-cell__ft"><i class="weui-icon-warn"></i></div>
  </div>
</template>

<script>
import $ from '../core/util'
import uuid from '../core/uuid'
import form from '../core/form'
import picker from '../picker'
import { Hours, Mins } from './time.js'

export default {
  name: 'time-picker',
  props: {
    value: {
      type: String,
      validator: val => {
        return val ? /^(20|21|22|23|[0-1]\d):[0-5]\d$/.test(val) : true
      }
    },
    title: String,
    placeholder: {
      type: String,
      default: ''
    },
    autoComplete: {
      type: Boolean,
      default: false
    },
    required: {
      type: Boolean,
      default: false
    },
    emptyTips: String,
    labelStyle: [String, Object],
    inputStyle: [String, Object]
  },
