              this.currentValue = tt
            }
          }
        },
        onConfirm: (result) => {
          let tt = `${result[0].value}:${result[1].value}`
          this.currentValue = tt
        }
      })
    }
  },
  watch: {
    value (val) {
      this.currentValue = val
    },
    currentValue (val) {
      this.$emit('on-change', val)
      this.$emit('input', val)
    }
  }
}
</script>

<style lang="less">
@import '../../styles/weui/widget/weui-cell/weui-access';
@import '../../styles/weui/widget/weui-cell/weui-form/weui-form_common';
@import '../../styles/weui/widget/weui-animate/weui-animate.less';
