export default function (color) {
    let ret
    switch (color) {
      case 'green':
        ret = '#1aad19'
        break
      case 'yellow':
        ret = '#f7ba2a'
        break
      case 'purple':
        ret = '#800080'
        break
      case 'blue':
        ret = '#50bfff'
        break
      case 'red':
        ret = '#ff4949'
        break
      default:
        ret = color
    }
    return ret
}



