<template>
  <div v-nav="{title: ''}">
    <list :indent="false" title="ios ">
      <list-item @on-click="show1 = true"></list-item>
      <list-item @on-click="show2 = true"></list-item>
      <list-item @on-click="show3 = true"></list-item>
      <list-item @on-click="show4 = true">Mask</list-item>
      <list-item @on-click="show5 = true"></list-item>
      <list-item @on-click="show6 = true"></list-item>
      <list-item @on-click="show7 = true"></list-item>
    </list>
    <list :indent="false" title="android ">
      <list-item @on-click="show8 = true"></list-item>
    </list>
    <action-sheet v-model="show1">
      <action-sheet-item></action-sheet-item>
      <action-sheet-item></action-sheet-item>
      <action-sheet-item></action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show2" title="">
      <action-sheet-item></action-sheet-item>
      <action-sheet-item></action-sheet-item>
      <action-sheet-item></action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show3" title="" :show-cancel="false">
      <action-sheet-item></action-sheet-item>
      <action-sheet-item></action-sheet-item>
      <action-sheet-item></action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show4" :hide-on-clicking-mask="false">
      <action-sheet-item></action-sheet-item>
      <action-sheet-item></action-sheet-item>
      <action-sheet-item></action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show5">
      <div slot="header">
          <span style="color:#000;font-size:18px;line-height:32px;"></span>
          <p></p>
      </div>
      <action-sheet-item></action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show6">
      <action-sheet-item style="color:#ff0000"></action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show7">
      <action-sheet-item @on-click="menu1()"></action-sheet-item>
      <action-sheet-item @on-click="menu2()"></action-sheet-item>
      <action-sheet-item></action-sheet-item>
    </action-sheet>
    <action-sheet v-model="show8" theme="android">
      <action-sheet-item></action-sheet-item>
      <action-sheet-item></action-sheet-item>
      <action-sheet-item></action-sheet-item>
    </action-sheet>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'
import { ActionSheet, ActionSheetItem } from '../components/action-sheet'

export default {
  components: {
    List,
    ListItem,
    ActionSheet,
    ActionSheetItem
  },
  data () {
    return {
      show1: false,
      show2: false,
      show3: false,
      show4: false,
      show5: false,
      show6: false,
      show7: false,
      show8: false
    }
  },
  methods: {
    menu1 () {
      this.$toast.show('')
    },
    menu2 () {
      this.$loading.show('')
      setTimeout(() => {
        this.$loading.hide()
      }, 2000)
    }
  }
}
</script>



