import $ from '../core/util';
import tpl from './gallery.html';

let _sington;

/**
 * gallery 
 * @param {string} url galleryurl
 * @param {object=} options 
 * @param {string=} options.className 
 * @param {function=} options.onDelete 
 *
 * @example
 * var gallery = weui.gallery(url, {
 *     className: 'custom-classname',
 *     onDelete: function(){
 *         if(confirm('')){ console.log(''); }
 *         gallery.hide(function() {
 *              console.log('`gallery` has been hidden');
 *          });
 *     }
 * });
 */
function gallery(url, options = {}) {
    if(_sington) return _sington;

    options = $.extend({
        className: '',
        onDelete: $.noop
    }, options);

    const $gallery = $($.render(tpl, $.extend({
        url: url
    }, options)));

    function _hide(callback){
        _hide = $.noop; // 

        $gallery
            .addClass('weui-animate-fade-out')
            .on('animationend webkitAnimationEnd', function () {
                $gallery.remove();
                _sington = false;
                callback && callback();
            });
    }
    function hide(callback){ _hide(callback); }

    $('body').append($gallery);
    $gallery.find('.weui-gallery__img').on('click', function () { hide(); });
    $gallery.find('.weui-gallery__del').on('click', function () {
        options.onDelete.call(this, url);
    });

    $gallery.show().addClass('weui-animate-fade-in');

    _sington = $gallery[0];
    _sington.hide = hide;
    return _sington;
}
export default gallery;



