<template>
  <div class="x-loading" v-show="show">
    <div class="weui-mask_transparent"></div>
    <div class="weui-toast">
      <i class="weui-loading weui-icon_toast"></i>
      <p class="weui-toast__content">{{text}}<slot></slot></p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'loading',
  props: {
    value: Boolean,
    text: String
  },
  data () {
    return {
      show: false
    }
  },
  watch: {
    show (val) {
      this.$emit('input', val)
    },
    value (val) {
      this.show = val
    }
  }
}
</script>

<style lang="less">
@import '../../styles/weui/widget/weui-tips/weui-mask';
@import '../../styles/weui/widget/weui-tips/weui-toast';
@import '../../styles/weui/widget/weui-loading/weui-loading';
</style>



