
// If you are using `Promise`, import this module to fix the lower version webkit
// or ie does not support 'Promise' issue.
import 'es6-promise/auto'

import Vue from 'vue'
import App from './App'
import router from './router'

// The plugin for Vue.js provides services for making web requests and handle
// responses using a XMLHttpRequest or JSONP
// see: https://github.com/pagekit/vue-resource
import resource from 'vue-resource'

import { store, navigation, Alert, Confirm, Loading, Toast, Tooltip, Form } from 'vue-weui-x'

Vue.use(resource)

// Use VWX plugins
Vue.use(Alert)
Vue.use(Confirm)
Vue.use(Toast)
Vue.use(Loading)
Vue.use(Tooltip)
Vue.use(Form)

// Use `fastclick` to eliminate the 300ms delay between a physical tap and the
// firing of a click event on mobile browsers.
// see: https://github.com/ftlabs/fastclick
const FastClick = require('fastclick')
FastClick.attach(document.body)

// register navigation guards
navigation.ready()
navigation.bind(router, store)

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app')



