<template>
  <div v-nav="{title: ''}">
    <list :indent="false" title="">
      <list-item @on-click="show1 = true">1</list-item>
      <list-item @on-click="show2 = true">2</list-item>
    </list>
    <list :indent="false" title="">
      <list-item @on-click="show3 = true">1Scroller</list-item>
    </list>
    <popup v-model="show1">
      <grid>
        <grid-item label="QQ" :icon="qq"></grid-item>
        <grid-item label="QQ" :icon="zone"></grid-item>
        <grid-item label="" :icon="sina"></grid-item>
        <grid-item label="" :icon="webchat"></grid-item>
        <grid-item label="" :icon="friend"></grid-item>
        <grid-item label="" :icon="more"></grid-item>
      </grid>
    </popup>
    <popup v-model="show2">
      <list :indent="false">
        <list-item desc=""><p></p></list-item>
        <list-item desc=""><p></p></list-item>
      </list>
    </popup>
    <popup v-model="show3" :full-screen="true" title="">
      <scroller ref="my_scroller">
        <list>
          <list-item v-for="(item, index) in items" :key="index">
            {{ item }}
          </list-item>
        </list>
      </scroller>
    </popup>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'
import { Grid, GridItem } from '../components/grid'
import Popup from '../components/popup'
import Icon from '../components/icon'
import Scroller from '../components/scroll'
import QQIcon from '../assets/media/popup_qq.png'
import QQZoneIcon from '../assets/media/popup_zone.png'
import SINAIcon from '../assets/media/popup_sina.png'
import WebchatIcon from '../assets/media/popup_wechat.png'
import FriendIcon from '../assets/media/popup_friend.png'
import MoreIcon from '../assets/media/popup_more.png'

export default {
  components: {
    List,
    ListItem,
    Grid,
    GridItem,
    Icon,
    Popup,
    Scroller
  },
  data () {
    return {
      qq: QQIcon,
      zone: QQZoneIcon,
      sina: SINAIcon,
      webchat: WebchatIcon,
      friend: FriendIcon,
      more: MoreIcon,
      items: [],
      show1: false,
      show2: false,
      show3: false
    }
  },
  mounted () {
    for (let i = 1; i <= 20; i++) {
      this.items.push('Mobile web UI based on Vue - ' + i)
    }
    setTimeout(() => {
      this.$refs.my_scroller.resize()
    })
  }
}
</script>

<style lang="less" scoped>
.weui-grid {
  &:before {
    border-right-width:0;
  }
  &:after {
    border-bottom-width:0;
  }
}
</style>



