<template>
  <div v-nav="{title: ''}">
    <div id="form">
      <group type="form" title="">
        <x-input title="" placeholder="" required></x-input>
      </group>
      <group type="form" title="/">
        <x-input v-model="model1" title="" placeholder="" readonly></x-input>
        <x-input v-model="model2" title="" placeholder="" disabled></x-input>
      </group>
      <group type="form" title="">
        <x-input type="tel" title="" pattern="^\d{11}$" placeholder="" not-match-tips="" :max-length="11"></x-input>
        <x-input title="" pattern="/(?:^\d{15}$)|(?:^\d{18}$)|^\d{17}[\dXx]$/" placeholder="" not-match-tips=""></x-input>
      </group>
      <group type="form" title="change">
        <x-input v-model="model3" title="" placeholder="" @on-change="change"></x-input>
        <x-input v-model="model4" title="" readonly></x-input>
      </group>
      <group type="form" title="focus">
        <x-input type="number" title="" placeholder="" @on-focus="focus"></x-input>
      </group>
      <group type="form" title="">
        <x-input placeholder="" ></x-input>
      </group>
      <group type="form" title="">
        <x-input type="tel" title="" pattern="^\d{11}$" placeholder="" not-match-tips="" :max-length="11" required>
          <button class="vcode-btn" slot="right"></button>
        </x-input>
        <x-input title="" placeholder="" pattern="^[A-Za-z0-9]{4}$" :max-length="4" required empty-tips="">
          <img class="vcode" :src="vcode" slot="right">
        </x-input>
      </group>
      <group type="form" title="()" labelStyle="width:120px">
        <x-input title="" placeholder="inputlabel"></x-input>
        <x-input title="" placeholder="inputlabel" labelStyle="width:100px;"></x-input>
      </group>
      <group type="form" title="" inputStyle="color:#f43530;">
        <x-input v-model="model5" title="" placeholder="inputlabel"></x-input>
        <x-input v-model="model6" title="" placeholder="inputlabel" inputStyle="text-align:right"></x-input>
      </group>
      <group type="form" title="">
        <x-input v-model="model7" title="" placeholder="" readonly>
          <button class="scale" slot="right" @click="click"><icon name="qrcode" :scale="1.3"></icon></button>
        </x-input>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit"></x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import XInput from '../components/input'
import Icon from '../components/icon'
import XButton from '../components/button'

export default {
  components: {
    Group,
    XInput,
    Icon,
    XButton
  },
  data () {
    return {
      model1: '',
      model2: '',
      model3: '',
      model4: '',
      model5: '',
      model6: '',
      model7: '',
      vcode: 'data:image/jpeg;base64,'
    }
  },
  methods: {
    change (val) {
      this.model4 = val
    },
    focus () {
      this.$toast.show('')
    },
    click () {
      this.model7 = Math.random().toString(36).substring(3, 15).toUpperCase()
    },
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('')
        }
      })
    }
  }
}
</script>

<style scoped>
.vcode-btn {
  background-color: transparent;
  border-top: 0;
  border-right: 0;
  border-bottom: 0;
  outline: 0;
  display: inline-block;
  height: 45px;
  margin-left: 5px;
  padding: 0 0.7em;
  border-left: 1px solid #E5E5E5;
  line-height: 45px;
  vertical-align: middle;
  font-size: 17px;
  color: #09BB07;
}
.vcode {
  margin-left: 5px;
  height: 45px;
  vertical-align: middle;
}
.scale {
  background-color: transparent;
  border-top: 0;
  border-right: 0;
  border-bottom: 0;
  outline: 0;
  display: inline-block;
  height: 45px;
  margin-left: 5px;
  padding: 0.6em 0.7em 0 0.7em;
  border-left: 0;
  line-height: 45px;
}
</style>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



