<template>
  <div v-nav="{title: ''}">
    <list title="">
      <list-item desc=""><p></p></list-item>
    </list>
    <list title="IOS ">
      <list-item desc=""><p></p></list-item>
      <list-item desc=""><p></p></list-item>
    </list>
    <list :indent="false" title="">
      <list-item desc=""><p></p></list-item>
      <list-item desc=""><p></p></list-item>
    </list>
    <list title="">
      <list-item desc="">
        <p></p>
        <div slot="icon">
          <img slot="icon" width="20" style="display:block;margin-right:5px;" :src="img">
        </div>
      </list-item>
    </list>
    <list title="">
      <list-item :link="{path: '/demo/list'}"><p></p></list-item>
      <list-item desc="" :link="{path: '/demo/list'}"><p></p></list-item>
      <list-item desc="" :link="{path: '/demo/list'}">
        <p></p>
        <div slot="icon">
          <img slot="icon" width="20" style="display:block;margin-right:5px;" :src="img">
        </div>
      </list-item>
    </list>
    <list title="">
      <list-item :link="{path: '/demo/list'}"><p></p><p style="color:#aaa;">ddd</p></list-item>
      <list-item :link="{path: '/demo/list'}" :body-style="bdstyle"><p></p></list-item>
    </list>
    <list title="">
      <list-item :link="{path: '/demo/list'}" desc=""><p></p><badge slot="desc" text="3"></badge></list-item>
      <list-item :link="{path: '/demo/list'}"><p></p><badge slot="desc"></badge></list-item>
    </list>
    <list title="">
      <list-item desc="" @on-click="onClick()"><p></p></list-item>
      <list-item desc="" :allow-access="false"><p></p></list-item>
    </list>
  </div>
</template>

<script>
import { List, ListItem, Badge } from 'vue-weui-x'

export default {
  components: {
    List,
    ListItem,
    Badge
  },
  data () {
    return {
      img: 'data:image/png;base64,',
      bdstyle: {'color': '#09bb07'}
    }
  },
  methods: {
    onClick () {
      this.$toast.show({text: ',', position: 'bottom', width: '200px'})
    }
  }
}
</script>



