<template>
  <div class="weui-actionsheet__cell" @click="onClicked()">
    <slot>{{text}}</slot>
  </div>
</template>

<script>
import { go } from '../core/router'

export default {
  name: 'action-sheet-item',
  props: {
    text: String,
    link: {
      type: [String, Object]
    }
  },
  methods: {
    onClicked () {
      this.$parent.hide()
      this.$nextTick(() => {
        if (this.link) {
          go(this.link, this.$router)
        } else {
          this.$emit('on-click')
        }
      })
    }
  }
}
</script>



