<template>
  <div>
    <scroller ref="scroller">
      <div v-for="(item, index) in demo" :key="index">
        <grid :cols="1" style="margin-bottom:-1px;margin-top:10px;">
          <grid-item>
              <span class="title" slot="label">{{item.name}}</span>
          </grid-item>
        </grid>
        <grid>
          <grid-item v-for="subItem in item.components" :key="subItem.name" :label="subItem.name" :icon="subItem.icon" :link="{path: subItem.path}"></grid-item>
        </grid>
      </div>
      <div style="padding-top:10px"></div>
    </scroller>
  </div>
</template>

<script>
import Scroller from '../components/scroll'
import { Grid, GridItem } from '../components/grid'
import Demo from '../demo.json'

export default {
  name: 'demo',
  components: {
    Scroller,
    Grid,
    GridItem
  },
  data () {
    return {
      demo: Demo
    }
  },
  activated () {
    setTimeout(() => {
      this.$refs.scroller.resize()
    }, 10)
    // use store mutation instead of `v-nav` directive in keepalive view
    this.$store.dispatch('updateNavBar', {title: '', showMenuButton: false})
  }
}
</script>

<style scoped>
.weui-grids {
  background-color: #fff;
}
.title {
  font-size:18px;
  color:#555;
}
</style>



