<template>
  <div v-nav="{title: ''}">
    <div id="form">
      <group type="radio" title="">
        <x-radio v-model="v1" required :options="options" name="radio1" empty-tips=""></x-radio>
      </group>
      <group type="radio" title="">
        <x-radio v-model="v2" :options="options" name="radio2" ></x-radio>
      </group>
      <group type="radio" title="">
        <x-radio v-model="v3" required :options="options2" name="radio3" empty-tips=""></x-radio>
      </group>
      <group type="radio" title="">
        <x-radio v-model="v4" :options="options3" name="radio4"></x-radio>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit"></x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import XRadio from '../components/radio'
import XButton from '../components/button'

export default {
  components: {
    Group,
    XRadio,
    XButton
  },
  data () {
    return {
      options: [
        {label: '', value: 'male'},
        {label: '', value: 'female'}
      ],
      options2: [
        {label: '', value: '1'},
        {label: '', value: '2'},
        {label: '', value: '3'},
        {label: ' ()', value: '4', disabled: true}
      ],
      options3: [
        {label: '1', value: '10', icon: 'data:image/png;base64,'},
        {label: '2', value: '20', icon: 'data:image/png;base64,'}
      ],
      v1: '',
      v2: 'male',
      v3: '',
      v4: ''
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



