<template>
  <div class="x-swiper" >
    <slot></slot>
  </div>
</template>

<script>
import Swiper from './swiper.js'

export default {
  name: 'swiper',
  props: {
    direction: {
      type: String,
      default: 'vertical',
      validator (value) {
        return [
          'horizontal',
          'vertical'
        ].indexOf(value) > -1
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      let swiper = new Swiper({
        direction: this.direction
      })
      swiper.on('swiped', (from, next) => {
        this.$emit('on-change', next)
      })
    })
  }
}
</script>

<style lang="less">
@import '../../styles/xui/x-swiper';
</style>



