<template>
  <div v-nav="{title: ''}">
    <div id="form">
      <group type="radio" title="">
        <label-radio v-model="v1" required :options="options" name="radio1" empty-tips=""></label-radio>
      </group>
      <group type="radio" title="">
        <label-radio v-model="v2" :options="options" name="radio2" ></label-radio>
      </group>
      <group type="radio" title="">
        <label-radio v-model="v3" required :options="options2" name="radio3" empty-tips=""></label-radio>
      </group>
      <group type="radio" title="">
        <label-radio v-model="v4" :options="options2" name="radio4" :cols="4"></label-radio>
      </group>
      <group type="radio" title="">
        <label-radio v-model="v5" :options="options" name="radio5" active-color="pink"></label-radio>
      </group>
    </div>
    <div style="padding:30px 20px">
      <x-button color="primary" @click.native="submit"></x-button>
    </div>
  </div>
</template>

<script>
import Group from '../components/group'
import LabelRadio from '../components/label-radio'
import XButton from '../components/button'

export default {
  components: {
    Group,
    LabelRadio,
    XButton
  },
  data () {
    return {
      options: [
        {label: '', value: 'male'},
        {label: '', value: 'female'}
      ],
      options2: [
        {label: '', value: '1'},
        {label: '', value: '2'},
        {label: '', value: '3'},
        {label: '', value: '4', disabled: true}
      ],
      v1: '',
      v2: 'male',
      v3: '',
      v4: '',
      v5: 'female'
    }
  },
  methods: {
    submit () {
      this.$form.validate('#form', this, (error) => {
        if (!error) {
          this.$toast.show('')
        }
      })
    }
  }
}
</script>

<style>
.weui-toptips {
  top: 44px !important;
}
</style>



