<template>
  <div v-nav="{title: ''}">
    <x-line>  </x-line>
    <div style="padding:0 20px;">
      <x-progress :percentage="0"></x-progress>
      <x-progress :percentage="70"></x-progress>
      <x-progress :percentage="100"></x-progress>
    </div>
    <x-line>  </x-line>
    <div style="padding:0 20px;">
      <x-progress :percentage="90"></x-progress>
      <x-progress :percentage="80" stroke-color="green"></x-progress>
      <x-progress :percentage="70" stroke-color="yellow"></x-progress>
      <x-progress :percentage="60" stroke-color="purple"></x-progress>
      <x-progress :percentage="50" stroke-color="blue"></x-progress>
      <x-progress :percentage="40" stroke-color="red"></x-progress>
      <x-progress :percentage="30" stroke-color="#000000"></x-progress>
    </div>
    <x-line>  </x-line>
    <div style="padding:0 20px;">
      <x-progress :percentage="80" :stroke-width="10"></x-progress>
      <x-progress :percentage="70" :stroke-width="10" stroke-color="green"></x-progress>
    </div>
    <x-line>  </x-line>
    <div style="padding:0 20px;">
      <x-progress :percentage="80" :stroke-width="20" :text-inside="true"></x-progress>
    </div>
    <x-line>  </x-line>
    <div style="padding:0 20px;display:block;">
      <x-progress :percentage="80" class="custom">80%</x-progress>
      <x-progress :percentage="80" :stroke-width="20" :text-inside="true">80%</x-progress>
    </div>
    <x-line>  </x-line>
    <div style="padding:0 20px;display:block;">
      <x-progress :percentage="80" :show-text="false"></x-progress>
    </div>
    <x-line>  </x-line>
    <div style="padding:0 20px;text-align: center;">
      <x-progress :percentage="0" type="circle"></x-progress>
      <x-progress :percentage="90" type="circle"></x-progress>
    </div>
    <x-line>  </x-line>
    <div style="padding:0 20px;text-align: center;">
      <x-progress :percentage="percentage" type="circle" stroke-color="yellow"></x-progress>
      <x-progress :percentage="percentage" type="circle" stroke-color="green"></x-progress>
      <br /><br />
      <slider v-model="percentage" style="margin: 0 30px;"></slider>
    </div>
    <x-line>  </x-line>
    <div style="padding:0 20px;text-align: center;">
      <x-progress :percentage="0" type="circle" :diameter="80"></x-progress>
      <x-progress :percentage="90" type="circle" :diameter="80" stroke-color="purple"></x-progress>
    </div>
    <x-line>  </x-line>
    <div style="padding:0 20px;text-align: center;">
      <x-progress :percentage="60" type="circle" stroke-color="blue" :reverse="true"></x-progress>
    </div>
    <x-line>  </x-line>
    <div style="padding:0 20px;text-align: center;">
      <x-progress :percentage="60" type="circle" stroke-color="red">60%</x-progress>
    </div>
    <x-line>  </x-line>
    <div style="padding:0 20px;text-align: center;">
      <x-progress :percentage="70" type="circle" :diameter="80" :text-inside="true"></x-progress>
    </div>
    <x-line title=""></x-line>
  </div>
</template>

<script>
import XLine from '../components/line'
import XProgress from '../components/progress'
import XButton from '../components/button'
import Slider from '../components/slider'

export default {
  components: {
    XLine,
    XProgress,
    XButton,
    Slider
  },
  data () {
    return {
      percentage: 50
    }
  }
}
</script>

<style>
.custom .x-progress__bar {
  padding-right: 80px;
  margin-right: -85px;
}
</style>



