const getHours = () => {
  let hours = []
  for (let i = 0; i < 24; i++) {
    if (i < 10) {
      hours.push({
        label: `0${i}`,
        value: `0${i}`
      })
    } else {
      hours.push({
        label: `${i}`,
        value: `${i}`
      })
    }
  }
  return hours
}

const getMins = () => {
  let mins = []
  for (let i = 0; i < 60; i++) {
    if (i < 10) {
      mins.push({
        label: `0${i}`,
        value: `0${i}`,
        t: ''
      })
    } else {
      mins.push({
        label: `${i}`,
        value: `${i}`,
        t: ''
      })
    }
  }
  return mins
}

let Hours = getHours()
let Mins = getMins()

export {
  Hours,
  Mins
}



