<template>
  <div class="x-popup" >
    <transition name="x-popup-mask">
      <div class="weui-mask" @click="hide()" v-show="show && !fullScreen"></div>
    </transition>
    <div class="weui-actionsheet" :class="{'weui-actionsheet_toggle': show, 'x-popup-full-screen': fullScreen}">
      <div class="weui-actionsheet__title" :class="{'x-box-shadow': show}" v-if="fullScreen">
        <p class="weui-actionsheet__title-text">{{title}}</p>
        <div class="x-close"  @click="hide()"><icon name="close" :scale="1.3"></icon></div>
      </div>
      <slot></slot>
    </div>
  </div>
</template>

<script>
import Icon from '../icon'

export default {
  name: 'popup',
  components: {
    Icon
  },
  props: {
    value: Boolean,
    title: String,
    fullScreen: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      show: false
    }
  },
  methods: {
    hide () {
      this.show = false
    }
  },
  watch: {
    show (val) {
      this.$emit('input', val)
      if (this.fullScreen) { // popout layer must be over navigation layer
        let body = document.querySelector('#body')
        if (val) {
          body.style.zIndex = 501
        } else {
          body.style.zIndex = 1
        }
      }
    },
    value (val) {
      this.show = val
    }
  }
}
</script>

<style lang="less">
@import '../../styles/weui/widget/weui-tips/weui-mask';
@import '../../styles/weui/widget/weui-tips/weui-actionsheet';

.x-popup-mask-enter,
.x-popup-mask-leave-active {
  opacity: 0;
}

.x-popup-mask-leave-active,
.x-popup-mask-enter-active{
  transition: opacity 0.2s linear 0s;
}

.x-popup {
  .weui-actionsheet {
    background-color: #fff;
  }
  .weui-actionsheet.x-popup-full-screen {
    background-color: #f4f4f4;
  }
}

.x-popup-full-screen {
  height: 100%;
  background-color: #f4f4f4;
  .weui-actionsheet__title {
    height: 44px;
    background: #fff;
    padding: 0 40px;
    color: #000;
    font-size: 18px;
    &.x-box-shadow {
      -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
    }
    &:before {
      border-bottom-width: 0;
    }
  }
  .x-close {
    position: absolute;
    top: 11px;
    right: 10px;
    bottom: 11px;
    color: #555;
  }
}

</style>



