<template>
  <div v-nav="{title: 'Marquee'}">
    <x-line></x-line>
    <marquee class="border">
      <marquee-item class="center item"></marquee-item>
      <marquee-item class="center item"></marquee-item>
      <marquee-item class="center item"></marquee-item>
      <marquee-item class="center item"></marquee-item>
      <marquee-item class="center item"></marquee-item>
      <marquee-item class="center item"></marquee-item>
    </marquee>
    <x-line></x-line>
    <marquee style="margin:0 20px; box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);">
      <marquee-item class="item2">
        <img width="50" style="display:block;float:left;" :src="sina">
        <div style="padding-left:55px;"><p></p><p class="text">,</p></div>
      </marquee-item>
      <marquee-item class="item2">
        <img width="50" style="display:block;float:left;" :src="webchat">
        <div style="padding-left:55px;"><p></p><p class="text">,</p></div>
      </marquee-item>
    </marquee>
    <x-line></x-line>
    <marquee async class="border" v-if="count > 0">
      <marquee-item v-for="i in count" :key="i" class="center item">{{i}}. VWX based on Vue and WeUI</marquee-item>
    </marquee>
  </div>
</template>

<script>
import XLine from '../components/line'
import { Marquee, MarqueeItem } from '../components/marquee'
import SINAIcon from '../assets/media/popup_sina.png'
import WebchatIcon from '../assets/media/popup_wechat.png'

export default {
  components: {
    XLine,
    Marquee,
    MarqueeItem
  },
  data () {
    return {
      count: 0,
      sina: SINAIcon,
      webchat: WebchatIcon
    }
  },
  mounted () {
    setTimeout(() => {
      this.count = 4
    }, 1000)
  }
}
</script>

<style scoped>
.border{
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.center {
  text-align: center;
}
.item {
  height: 34px;
  line-height: 34px;
  vertical-align: middle;
}
.item2 {
  vertical-align: middle;
  padding: 5px;
  background-color: #fff;
  border-radius: 4px;
}
.text {
  width: 240px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
  color: #aaa;
}
</style>



