<template>
  <div v-nav="{title: ''}">
    <list :indent="false" title="">
      <list-item @on-click="show1 = true"></list-item>
      <list-item @on-click="show2 = true"></list-item>
    </list>
    <x-dialog v-model="show1">
      <div style="height:32px;text-align:center;line-height:32px;"></div>
      <div class="box"></div>
      <div @click="show1=false" style="padding-top:5px;">
        <icon name="close" slot="icon" :scale="1.3"></icon>
      </div>
    </x-dialog>
    <x-dialog v-model="show2" :hide-on-blur="true">
      <div class="box">
      </div>
    </x-dialog>
  </div>
</template>

<script>
import { List, ListItem } from '../components/list'
import XDialog from '../components/dialog'
import Icon from '../components/icon'

export default {
  components: {
    List,
    ListItem,
    XDialog,
    Icon
  },
  data () {
    return {
      show1: false,
      show2: false
    }
  }
}
</script>

<style scoped>
  .box {
    height: 200px;
    width: 100%;
    background-color: #f4f4f4;
  }
</style>



