<template>
  <div v-nav="{title: ''}">
    <x-line></x-line>
    <slider v-model="percent1" :disabled="true" style="margin: 0 10px;"></slider>
    <x-line></x-line>
    <slider v-model="percent2" style="margin: 0 10px;"></slider>
    <x-line>step:10</x-line>
    <slider v-model="percent3" :step="10" style="margin: 0 10px;"></slider>
    <x-line></x-line>
    <slider v-model="percent4" style="margin: 0 10px;" color="green"></slider>
    <slider v-model="percent4" style="margin: 0 10px;" color="yellow"></slider>
    <slider v-model="percent4" style="margin: 0 10px;" color="purple"></slider>
    <slider v-model="percent4" style="margin: 0 10px;" color="blue"></slider>
    <slider v-model="percent4" style="margin: 0 10px;" color="red"></slider>
    <slider v-model="percent4" style="margin: 0 10px;" color="#000000"></slider>
  </div>
</template>

<script>
import XLine from '../components/line'
import Slider from '../components/slider'

export default {
  components: {
    XLine,
    Slider
  },
  data () {
    return {
      percent1: 40,
      percent2: 70,
      percent3: 0,
      percent4: 50
    }
  }
}
</script>



